.page-main {
  position: relative;
  background-color: $grey-1;
}

.page-main__title {
  width: 250px;
  margin: 0 auto;
  padding-top: 90px;

  font-size: 27px;
  font-weight: bold;

  line-height: 1.1;
  text-align: center;

  color: $black-1;
}

.page-main__title-wrap {
  z-index: 0;
  margin-top: -15px;
  position: relative;
  height: 375px;
  padding: 0;

  background-image: url("../img/hero-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: center;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/hero-mobile@2x.jpg");
    background-size: 575px auto;
  }
}

@media (min-width: 768px) {
  .page-main::after {
    position: absolute;
    bottom: -3px;
    left: -15px;
    content: "";

    width: 100%;
    height: 20px;
    background-image: url("../img/bg-zigzag-fill.svg");
  }

  .page-main__title-wrap {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    height: 746px;
    margin-top: 0;
    margin-bottom: 20px;
    padding: 0 50px;
    background-image: url("../img/hero-tablet@1x.jpg");
    border-top: 1px solid $white-2;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/hero-tablet@2x.jpg");
      background-size: 1150px auto;
    }
  }

  .page-main__title {
    width: 500px;
    margin-top: 65px;
    font-size: 43px;
  }
}

@media (min-width: 1150px) {
  .page-main::after {
    left: 0;
  }

  .page-main__title-wrap {
    margin-bottom: 0;
    background-image: url("../img/hero-desktop@1x.jpg");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/hero-desktop@2x.jpg");
      background-size: 1150px auto;
    }
  }

  .page-main__title {
    width: 650px;
    font-size: 53px;
  }
}
